import React from 'react';

const NAV_LINKS = [
  { label: 'Home', href: '/' },
  { label: 'Portfolio', href: '/portfolio' },
  { label: 'Insights', href: '/insights' },
  { label: 'Settings', href: '/settings' },
];

export default function NavBar() {
  // Determine active link
  const [active, setActive] = React.useState('');
  React.useEffect(() => {
    if (typeof window !== 'undefined') {
      setActive(window.location.pathname);
    }
  }, []);

  return (
    <header className="bg-gradient-to-r from-blue-50 via-white to-blue-100 border-b border-gray-200 shadow-sm">
      <nav className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 flex items-center h-16 justify-between">
        <div className="flex items-center gap-8">
          <span className="font-extrabold text-xl text-blue-700 tracking-tight drop-shadow-sm">HighWater Protocol</span>
          <ul className="flex gap-2 md:gap-4 lg:gap-6 bg-gray-50 rounded-full px-2 py-1 shadow-inner border border-gray-100">
            {NAV_LINKS.map(link => (
              <li key={link.href}>
                <a
                  href={link.href}
                  className={`px-4 py-2 rounded-full font-medium transition-all duration-150 text-sm focus:outline-none focus:ring-2 focus:ring-blue-200 hover:bg-blue-100 hover:text-blue-700 ${
                    active === link.href ? 'bg-blue-600 text-white shadow font-semibold' : 'text-gray-700'
                  }`}
                >
                  {link.label}
                </a>
              </li>
            ))}
          </ul>
        </div>
        <div className="flex items-center gap-4">
          <span className="text-xs text-gray-500 hidden md:inline">Carter Family Portfolio</span>
          <span className="text-xs text-gray-500 hidden md:inline">Logged in as James Carter</span>
        </div>
      </nav>
    </header>
  );
}
